#root{
  background: skyblue;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.inputdiv{
  margin: 1rem 0;
  background: green;
}

input{
  width: 10rem;
  padding: 0.5rem 1rem;
}

span{
  /* width: 1rem; */
  padding: 0.5rem 1rem;
  cursor: pointer;
  color: white;
}

.tasklist{
  width: 17rem;

}

.task{
  width: 88%;
  display: flex;
  justify-content: space-between;
  background-color: white;
  padding: 0.5rem 1rem;
  border: 1px solid red;
  border-radius: 2rem;
  margin: 0.5rem 0;
}

h3{
  display: inline-block;
  padding: 0;
  margin: auto .5rem;
  max-width: 15rem;
  max-height: 5rem;
  overflow: scroll;
  overflow-y: hidden;
}

.delete{
  margin: auto 0;
  background-color: #ff0000;
  padding: 0.5rem;
  max-height: 1.5rem;
}
.edit{
  background-color: rgb(46, 109, 67);
  margin: auto 0;
  padding: 0.5rem;
  margin-left: auto;
  margin-right: 0.5rem;
  max-height: 1.5rem;
}
.deleteall{
  background-color: rgb(129, 100, 100);
  border-radius: 1rem;
  color: white;
  padding: 0.5rem 1rem;
}
